@import url('../../sassStyles/_animations.scss');

#input {
    padding: 0;
    margin: 0;
    font-family: inherit;
    position: relative;
    vertical-align: bottom;
    top: -30px;
    left: 0;
    width: 100%;
    &::placeholder{
    color: rgb(153, 153, 153);
  }
    border: none;
    background-color: var(--background);
    font-size: 30px;
    &:focus {
        outline: none;
    };
    caret-color: rgba(0, 0, 0, 0);
}

#textarea {
  padding: 0;
  margin: 0;
  font-family: inherit;
  position: absolute;
  vertical-align: bottom;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 85vw;
  &::placeholder{
    color: rgb(153, 153, 153);
  }
  border: none;
  background-color: var(--background);
  font-size: 30px;
  &:focus {
      outline: none;
  };
  caret-color: rgba(0, 0, 0, 0);
    z-index: 80;
    resize: none;
}

#input:-webkit-autofill, #textarea:-webkit-autofill {
  border: none;
  -webkit-text-fill-color: var(--content);
  box-shadow: 0 0 0px 1000px var(--background) inset;
  -webkit-box-shadow: 0 0 0px 1000px var(--background) inset;
}

#input:-webkit-autofill:focus, #textarea:-webkit-autofill:focus {
  border: none;
  -webkit-text-fill-color: var(--content);
  box-shadow: 0 0 0px 1000px var(--background) inset;
  -webkit-box-shadow: 0 0 0px 1000px var(--background) inset;
}

#input:-internal-autofill-selected, #textarea:-internal-autofill-selected {
  background-image: none !important;
  background-color:  var(--background) !important;
  color: var(--content) !important;
  font-family: 'Lucida Console', monospace;
}

.caret_input {
  position: relative;
}

#input_element {
  display: flex;
  flex-direction: row;
  position: relative;
  min-width: fit-content;
  min-height: 24px;
}

#span {
  width: 100%;
  max-width: 84vw;
  height: 240px;
  overflow-y: scroll;
  overflow-wrap: anywhere;
}

#contact_msg {
  z-index: 120;
}

.span-before:before {  
  content: '';
  display: inline-block;
  width: 18px;
  height: 10px;
  vertical-align: middle;
  position: relative;
  padding: 0 0 0 0;
  margin: 0 0 -25px -19px;
  bottom: 8px;
  left: 19px;
  background: var(--content);
  animation: blink 1s steps(2, start) infinite;
  -moz-animation: blink 1s steps(2, start) infinite;
  -webkit-animation: blink 1s steps(2, start) infinite;
  z-index: 90;
}

.span-after:after {  
  content: '';
  display: inline-block;
  width: 20px;
  height: 10px;
  vertical-align: middle;
  position: relative;
  padding: 0 0 0 0;
  margin: 0 0 -25px -19px;
  bottom: 8px;
  left: 20px;
  background: var(--content);
  animation: blink 1s steps(2, start) infinite;
  -moz-animation: blink 1s steps(2, start) infinite;
  -webkit-animation: blink 1s steps(2, start) infinite;
  z-index: 90;
}

@media screen and (max-width: 600px) {
  .span-after:after {
    content: '';
    width: 12px;
    height: 5px;
    margin: 0 0 -20px -12px;
    bottom: 4px;
    left: 12px;
    display: inline-block;
    vertical-align: middle;
    position: relative;
  }
  
  .span-before:before {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    content: '';
    width: 12px;
    height: 5px;
    margin: 0 0 -20px -12px;
    bottom: 4px;
    left: 12px;
  }
}